body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.single_product{
  align-items: center;
}

.single_product img{
  width: 150px;
  height: 200px;
  object-fit: contain;
}
.hompage__pagination{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 20px;
}
.accoutng-img{
  height: 125px;
  width: 125px;
  margin:20px;
}

.navpanel{
  display: flex;
  width: 100%;
 }

.navpanel-right{
 margin-left: auto;
}

